body {
    margin: 0;
    font-family: 'M PLUS 1p', sans-serif;
    background-color: #212121;
    color: white;
}

.text1 {
    display: flex;
    justify-content: center;
    background-color: black;
}

.marvel-image {
    background-color: red;
    padding: 10px;
    border-radius: 10px;
}

.searchbox-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}


.search-main {
    position: relative;
    top: 100px;
}

.searchbox {
    padding: 10px;
    border-radius: 10px;
    border: none;
}

.search-btn {
    padding: 5px;
    margin-top: 20px;
    border: none;
    border-radius: 20px;
    width: 70px;
}

.search-btn-2 {
    padding: 6px;
    margin-top: 20px;
    border: none;
    border-radius: 20px;
    width: 100px;
}

.content {
    width: 60%;
    margin: 3rem auto;
    display: flex;
    justify-content: center;
}

.char {
    background-color: red;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 30px;
}

.char img {
    min-height: 300px;
    max-height: 500px;
    border-radius: inherit;
}

.char-info {
    text-align: center;
    width: 70%;
    padding: 10px;
}

.char h1 {
    color: white;
    text-align: center;
    background-color: black;
}

@media only screen and (max-width: 768px) {
    .char img {
        min-height: 200px;
        max-height: 250px;
    }

    .char {
        background-color: red;
        padding: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 20px;
    }

    .char-info {
        text-align: center;
        width: 100%;
        padding: 10px;
    }

    .search-head {
        text-align: center;
        width: 100%;
    }
}